@use 'base/variables' as v;
@use 'base/mixins' as mix;

.table {
    width: 100%;
    border-collapse: collapse;

    &__thead {
        background-color: v.$purpuraOscuro;
        color: v.$blanco;
    }
    &__th {
        padding: 1rem;
        text-align: left;
    }
    &__tr {
        &:nth-child(even) {
            background-color: lighten(v.$gris, 40%);
            color: v.$purpuraOscuro;
        }
    }
    &__td {
        padding: 1.5rem 1rem;
        &--acciones {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 2rem;
        }
    }

    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;
        transition-property: color;
        transition-duration: 0.5s;
        &--editar {
            color: v.$moradoClaro;
            &:hover {
                color: darken(v.$moradoClaro, 20%);
            }
        }
        &--eliminar {
            color: v.$rojo;
            &:hover {
                color: darken(v.$rojo, 20%);
            }
        }
        &:hover {
            cursor: pointer;
        }
    }
}