@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.contenedor-app {
    @include mix.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }
    
    .imagen {
        height: 20rem;
        background-image: linear-gradient(to right, rgba(0, 0, 0, 0,) 20%, v.$purpuraOscuro2 100%), url(../img/cuentaBanc.png);
        // background-image: url(../img/appsalon2.jpeg);
        background-size: cover;
        background-position: center center;
        @include mix.tablet {
            height: auto;
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include mix.tablet {
            overflow: scroll;
            overflow-x: hidden;
            &::-webkit-scrollbar {
                width: 0;
                background: transparent;
            }
            &::-webkit-scrollbar-thumb {
                background: v.$purpuraOscuro2;
            }
        }
    }
}
.barra {

    margin: v.$separacion 0;
    .boton2{
        margin: 0;
    }
    @include mix.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;    
    }
}