@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.acciones {
    margin-top: v.$separacion;
    @include mix.tablet {
        display: flex;
        justify-content: space-between;       
    }

    a{
        color: v.$gris;
        transition-property: color;
        transition-duration: 0.5s;
        display: block;
        margin-bottom: calc(v.$separacion/2);
        text-align: center;
        &:hover {
            color: v.$purpuraClaro;
        }
    }
}