@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.tabs {

    @include mix.tablet {
        display: flex;
        gap: 1rem;
    }

    button {
        background-color: v.$purpuraOscuro;
        border: none;
        color: v.$gris;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        transition: background-color, color;
        transition-duration: 0.5s;
        &:hover {
            cursor: pointer;
            background-color: v.$gris;
            color: v.$purpuraOscuro;
        }
        &.actual {
            background-color: v.$gris;
            color: v.$purpuraOscuro;
        }
    }
}