@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.contenido-resumen {

    p {
        margin-bottom: 1rem;
    }
    span {
        color: v.$purpuraNormal;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}


.contenedor-servicios-cita {

    background: v.$purpuraOscuro;
    padding: 1.5rem;
    border-radius: 0.2rem;
    text-align: center;
    margin: 1rem;
    transition-property: background;
    transition-duration: 0.3s;

    &:last-of-type {
        margin-bottom: v.$separacion;
    }

    p{
        color: v.$gris;
        margin: 0 0 calc(v.$separacion/4) 0;
    }

    &:hover {
        background: v.$gris;
        p{
            color: v.$purpuraOscuro;
        }
    }
}


