@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.alerta {
    padding: calc(v.$separacion/4);
    color: v.$gris;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;
    opacity: 1;
    animation-duration: opacity, 3s;

    p{
        margin: 0;
        font-size: 1.5rem;
    }

    &.error {
        background: linear-gradient(90deg, v.$purpuraOscuro2 50%, v.$purpuraOscuro 100%);
        animation-duration: opacity, 0, 3s;
    }
    &.exito { 
        background-color: v.$verdeClaro;
    }
}