  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background: #220433;
  background: linear-gradient(90deg, #11031a 50%, #220433 100%);
  color: #e1e1e1;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Mina", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Mina", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  background-color: #220433;
  display: flex;
  justify-content: center;
  padding: 1rem 0;
  border-radius: 0.3rem;
  color: #e1e1e1;
  text-align: center;
  margin: 5rem 8.5rem;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0) 20%, #11031a 100%), url(../img/cuentaBanc.png);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
    overflow-x: hidden;
  }
  .contenedor-app .app::-webkit-scrollbar {
    width: 0;
    background: transparent;
  }
  .contenedor-app .app::-webkit-scrollbar-thumb {
    background: #11031a;
  }
}

.barra {
  margin: 5rem 0;
}
.barra .boton2 {
  margin: 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background: #e1e1e1;
  padding: 3rem;
  border-radius: 0.2rem;
  transition-property: transform, background, color;
  transition-duration: 0.3s;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background: #220433;
}
.listado-servicios .servicio.seleccionado p, .listado-servicios .servicio.seleccionado .precio-servicio {
  color: #e1e1e1;
}
.listado-servicios .servicio p {
  color: #220433;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #440966;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .servicio .formulario, .listado-servicios .servicio .campo, .listado-servicios .servicio .boton2 {
  margin: 1rem 0 0 0;
}
.listado-servicios .servicio:hover {
  transform: scale(1.03);
  background: #220433;
  cursor: pointer;
}
.listado-servicios .servicio:hover p, .listado-servicios .servicio:hover .precio-servicio {
  color: #e1e1e1;
}
.listado-servicios .servicio:hover .boton2 {
  cursor: -webkit-grabbing;
  background-color: #1a1b15;
}
.listado-servicios .servicio .boton2 {
  background-color: #1a1b15;
}
.listado-servicios .servicio .boton2:hover {
  background-color: #282920;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #440966;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicios-cita {
  background: #220433;
  padding: 1.5rem;
  border-radius: 0.2rem;
  text-align: center;
  margin: 1rem;
  transition-property: background;
  transition-duration: 0.3s;
}
.contenedor-servicios-cita:last-of-type {
  margin-bottom: 5rem;
}
.contenedor-servicios-cita p {
  color: #e1e1e1;
  margin: 0 0 1.25rem 0;
}
.contenedor-servicios-cita:hover {
  background: #e1e1e1;
}
.contenedor-servicios-cita:hover p {
  color: #220433;
}

.citas {
  list-style: none;
  padding: 0;
}
.citas li {
  padding: 1rem;
  border-top: 1px solid #e1e1e1;
  transition-property: background-color;
  transition-duration: 0.8s;
}
.citas li p {
  font-weight: 700;
  color: #440966;
}
.citas li span {
  color: #e1e1e1;
  font-weight: 400;
}
.citas li .servicio {
  margin: 0;
  text-align: center;
  color: #e1e1e1;
  background-color: #220433;
  padding: 1rem 0;
}
.citas li .total {
  text-align: center;
  color: #e1e1e1;
  background-color: #220433;
  padding: 1rem 0;
}
.citas li:hover {
  background-color: #11031a;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
  transition-property: background-color, color;
  transition-duration: 0.5s;
}
.barra-servicios .boton:hover {
  background-color: #e1e1e1;
  color: #220433;
}

.no-proyectos {
  text-align: center;
}
.no-proyectos a {
  background-color: #3000A7;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.5s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  display: inline-block;
  margin-left: 1rem;
}
@media (min-width: 768px) {
  .no-proyectos a {
    width: auto;
  }
}
.no-proyectos a:hover {
  background-color: #3700c1;
  cursor: pointer;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background-color: #e1e1e1;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 1.5rem;
  line-height: 2;
  transition-property: background-color, scale, color;
  transition-duration: 1s;
  border-radius: 0.8rem;
}
.listado-proyectos .proyecto:hover {
  background-color: #220433;
  scale: 1.025;
}
.listado-proyectos .proyecto:hover .proyecto-info p {
  color: #e1e1e1;
}
.listado-proyectos .proyecto .proyecto-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.listado-proyectos .proyecto .proyecto-info .proyecto-num {
  font-size: 2rem;
  color: #220433;
}
.listado-proyectos .proyecto .proyecto-info p {
  font-size: 1.5rem;
  color: #220433;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #220433;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-child(even) {
  background-color: white;
  color: #220433;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
  transition-property: color;
  transition-duration: 0.5s;
}
.table__accion--editar {
  color: #8655FF;
}
.table__accion--editar:hover {
  color: #4500ee;
}
.table__accion--eliminar {
  color: #cb0000;
}
.table__accion--eliminar:hover {
  color: #650000;
}
.table__accion:hover {
  cursor: pointer;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input, .campo select {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 0.3rem;
  transition-property: background-color;
  transition-duration: 0.5s;
}
.campo input:hover, .campo select:hover {
  background-color: #220433;
  color: #e1e1e1;
}
.campo input:disabled, .campo select:disabled {
  background-color: #220433;
  color: #e1e1e1;
  cursor: not-allowed;
}
.campo .boton, .campo .boton-alta {
  margin: 0;
  border-radius: 0.5rem;
  transition-property: background-color, color;
  transition-duration: 1s;
}
.campo .boton:hover, .campo .boton-alta:hover {
  background-color: #e1e1e1;
  color: #220433;
}

.boton {
