@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.citas {
    list-style: none;
    padding: 0;
    li{
        padding: 1rem;
        border-top: 1px solid v.$gris;
        transition-property: background-color;
        transition-duration: 0.8s;
        
        p{
            font-weight: v.$bold;
            color: v.$purpuraNormal;
        }
        span{
            color: v.$gris;
            font-weight: v.$regular;
        }
        .servicio {
            margin: 0;
            text-align: center;
            color: v.$gris;
            background-color: v.$purpuraOscuro;
            padding: 1rem 0;
        }
        .total {
            text-align: center;
            color: v.$gris;
            background-color: v.$purpuraOscuro;
            padding: 1rem 0;
        }
        &:hover {
            background-color: v.$purpuraOscuro2;
        }
    }
}
.barra-servicios {
    margin-bottom: v.$separacion;
    @include mix.tablet {
        display: flex;
        justify-content: space-between;

    }
    .boton {
        margin: 0 0 1rem 0;
        transition-property: background-color, color;
        transition-duration: 0.5s;
        &:hover {
            background-color: v.$gris;
            color: v.$purpuraOscuro;
        }
    }
}