@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.no-proyectos {
    text-align: center;
    a{
        @include mix.boton(v.$moradoOscuro, v.$blanco);
        display: inline-block;
        margin-left: 1rem;
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include mix.grid(1, 2rem);

    @include mix.tablet {
        @include mix.grid(2, 2rem);
    }
    @include mix.desktop {
        @include mix.grid(3, 2rem);
    }

    .proyecto {
        background-color: v.$gris;
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 1.5rem;
        line-height: 2;
        transition-property: background-color, scale, color;
        transition-duration: 1s;
        border-radius: 0.8rem;

        &:hover {
            background-color: v.$purpuraOscuro;
            scale: 1.025;
            .proyecto-info {
                p {
                    color: v.$gris;
                }
            }
        }

        .proyecto-info {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            .proyecto-num {
                font-size: 2rem;
                color: v.$purpuraOscuro;
            }
            p{
                font-size: 1.5rem;
                color: v.$purpuraOscuro;
            }
        }

    }
}