@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.listado-servicios {
    display: grid;
    gap: 2rem;

    @include mix.tablet {
        @include mix.grid(2, 1rem);
    }

    .servicio {
        background: v.$gris;
        padding: 3rem;
        border-radius: 0.2rem;
        transition-property: transform, background, color;
        transition-duration: 0.3s;
        text-align: center;
        &.seleccionado {
            background: v.$purpuraOscuro;
            p, .precio-servicio {
                color: v.$gris;
            }
        }

        p{

            color: v.$purpuraOscuro;
            margin: 0 0 calc(v.$separacion/4) 0;
        }
        .precio-servicio {
            color: v.$purpuraNormal;

            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
        .formulario, .campo, .boton2{
            margin: 1rem 0 0 0;
        }

        &:hover {
            transform: scale(1.03);
            background: v.$purpuraOscuro;
            cursor: pointer;
            p, .precio-servicio {
                color: v.$gris;
            }
            .boton2 {
                cursor: -webkit-grabbing;
                background-color: v.$negro;
            }
        }
        .boton2 {
            background-color: v.$negro;
            &:hover {
                background-color: lighten(v.$negro, 5%);
            }
        }
        
    }
}