@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.boton {
    @include mix.boton (v.$purpuraOscuro, v.$gris);
}
.boton2 {
    @include mix.boton (v.$negro, v.$gris);
}
.boton-eliminar {
    @include mix.boton (v.$moradoOscuro, v.$gris);
}
.boton-alta {
    @include mix.boton (v.$verdeClaro, v.$gris);
}