@use 'base/variables' as v;
@use 'base/mixins' as mix; 

.formulario {
    margin-top: 5rem;
    width: 100%;
}
.campo {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label {
        flex: 0 0 10rem;
    }
    input, select {
        flex: 1;
        border: none;
        padding: calc(v.$separacion/4);
        border-radius: 0.3rem;
        transition-property: background-color;
        transition-duration: 0.5s;
        &:hover {
            background-color: v.$purpuraOscuro;
            color: v.$gris;
        }
        &:disabled {
            background-color: v.$purpuraOscuro;
            color: v.$gris;
            cursor: not-allowed;
        }
    }

    .boton, .boton-alta {
        margin: 0;
        border-radius: 0.5rem;
        transition-property: background-color, color;
        transition-duration: 1s;
        &:hover {
            background-color: v.$gris;
            color: v.$purpuraOscuro;
        }
    }

}
